<template lang="pug">
    section(class="portfolio")
        div(class="container")
            div(class="title")
                h2 {{ $t("projects") }} 
                    span {{ $t("done") }} 
            div(class="portfolio-content")
                div(class="row")
                    div(class="col-md-6 col-lg-4 box" v-for="project in PortfolioData.projects", :key="project.id")
                        div(class="portfolio-box wow fadeInUp" data-wow-duration="1s")
                            div(class="portfolio-image")
                                img(:src="project.image", alt="")
                                h3 {{ project.title }}
                            div(class= "portfolio-text")
                                p {{ project.body }}
                            div(class="portfolio-buttons")
                                a(href="" class="btn hvr-sweep-to-bottom") {{ $t("project-link") }} 
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Portfolio",
    metaInfo() {
        return {
            title: process.env.VUE_APP_TITLE,
            titleTemplate: `%s | ${this.PortfolioData.website_title}`,
            meta: [
                { charset: "utf-8" },
                {
                    name: "description",
                    content: this.PortfolioData.meta_description,
                },
                {
                    name: "keywords",
                    content: this.PortfolioData.meta_keywords,
                },
            ],
        };
    },
    mounted() {
        this.$store.dispatch("getPortfolioData");
    },
    computed: {
        ...mapState(["PortfolioData"]),
    },
};
</script>
